import React, {memo, useEffect, useState} from 'react';
import {loadClassData} from "../scripts/classes";
import classData from "../data/classes.json";
import classAbvToDept from "../data/classAbvToDept.json";
import { useSpring, animated } from 'react-spring';


function ClassesSection() {

  const sp4 = useSpring({
    opacity: 1,
    transform: "translateY(0px)",
    from: { opacity: 0, transform: "translateY(-20px)" },
    delay: 600
  });

  useEffect(() => {
    loadClassData(classData, classAbvToDept);
  }, []);

  return (
    <section id="classes-container" aria-label="classes I've taken">
      <animated.div style={sp4} id="classes-header">
        <h1>Classes I've taken at UW</h1>
      </animated.div>
      <animated.div style={sp4} className="border-scribble"></animated.div>
      <animated.div style={sp4} id="class-selectors-container">
        <div id="class-selectors">
          <div id="class-force-select" aria-label="Change how classes are organized">
            <div>
              <p>Sort by</p>
              <span className="selected-class-style">major</span>
            </div>
            <ul className="nodisplay">
              <li>major</li>
              <li>number</li>
              <li>pre-reqs</li>
            </ul>
          </div>
          <div id="class-color-select">
            <div>
              <p>Color</p>
              <span className="selected-class-style">department</span>
            </div>
            <ul className="nodisplay" aria-label="Change how classes are colored">
              <li>department</li>
              <li>number</li>
              <li>credits</li>
              {/*<li>grade</li>*/}
            </ul>
          </div>
        </div>
      </animated.div>
      <div id="graph-container">
        <svg id="class-graph"></svg>
        <svg id="class-legend"></svg>
      </div>
    </section>
  );
}

export default memo(ClassesSection);