import {memo} from 'react';
import Project from './Project';
import projectData from "../data/projects.json";
import ProjectsImg from "../img/icons/projects-icon.png";
import { animated, useSpring } from 'react-spring';

export type ProjectType = {
  startDate: string
  endDate: string,
  link: string,
  name: string,
  org: string,
  role: string,
}

function ProjectsSection() {
  const sp1 = useSpring({
    opacity: 1,
    transform: "translateY(0px)",
    from: { opacity: 0, transform: "translateY(-20px)" },
  });

  const projects = [];
  for (let entry of projectData) {
    projects.push(
      <Project //img={""}
        name={entry.name}
        role={entry.role}
        org={entry.org}
        startDate={entry.startDate}
        endDate={entry.endDate}
        link={entry.link}
        key={"project-" + entry.name}/>
    );
  }

  const sp3 = useSpring({
    opacity: 1,
    transform: "translateY(0px)",
    from: { opacity: 0, transform: "translateY(-20px)" },
    delay: 400
  });

  return (
    <animated.section style={sp3} id="projects-section" aria-label="My projects">
      <div id="projects-header">
        <h1>Other projects</h1>
        <img src={ProjectsImg} alt={"crude line art of a computer"}/>
      </div>
      <div className="border-scribble"></div>
      <table>
        <thead>
          <tr>
            <th scope='col'>Name</th>
            <th scope='col'>Role</th>
            <th scope='col'>Organization</th>
            <th scope='col'>Date</th>
          </tr>
        </thead>
        <tbody>
          {projects}
        </tbody>
      </table>
    </animated.section>
  );
}

export default memo(ProjectsSection);