import React, {memo} from 'react';

function Publication(props: {//img: any,
                         title: string,
                         authors: string,
                         venue: string | null,
                         paperLink: string,
                         codeLink: string | null,
                         videoLink: string | null}) {
  return (
    <tr className="publication">
      <td><a href={props.paperLink} target="_blank" rel="noreferrer" tabIndex={0}>{props.title}</a></td>
      <td dangerouslySetInnerHTML={{__html: props.authors}}></td>
      <td>{props.venue}</td>
      <td>
        {(props.codeLink || props.videoLink) && <div className="pub-links">
          {props.codeLink && <a href={props.codeLink} target="_blank" rel="noreferrer" tabIndex={0}>code</a>}
          {props.videoLink && <a href={props.videoLink} target="_blank" rel="noreferrer" tabIndex={0}>video</a>}
        </div>}
      </td>
    </tr>
  );
}

export default memo(Publication);