import React, {memo} from 'react';

function Project(props: {//img: any,
                         name: string,
                         org: string,
                         role: string,
                         startDate: string,
                         endDate: string,
                         link: string}) {

  return (
    <tr className="project" tabIndex={0} onClick={() => window.open(props.link, '_blank')}>
      {/*<img src={props.img} alt={"Image for " + props.name}></img>*/}
      <td>{props.name}</td>
      <td>{props.role}</td>
      <td>{props.org}</td>
      <td>{(props.startDate === props.endDate) ? props.startDate :
           props.startDate + " to " + props.endDate}</td>
    </tr>
  );
}

export default memo(Project);