import React, { memo } from 'react';
import publicationsData from "../data/publications.json";
import Publication from './Publication';
import ClassesImg from "../img/icons/classes-icon.png";
import { animated, useSpring } from 'react-spring';

function PublicationsSection() {
    const publications = [];
    console.log(publicationsData)
    for (let entry of publicationsData) {
        publications.push(
        <Publication
            title={entry.title}
            authors={entry.authors}
            venue={entry.venue}
            paperLink={entry.paperLink}
            codeLink={entry.codeLink}
            videoLink={entry.videoLink}
            key={"pub-" + entry.title}/>
        );
    }

    const sp2 = useSpring({
        opacity: 1,
        transform: "translateY(0px)",
        from: { opacity: 0, transform: "translateY(-20px)" },
        delay: 200
    });

    return (
        <animated.section style={sp2} id="publications-section">
            <div id="publications-header">
                <h1>Publications</h1>
                <img src={ClassesImg} alt={"crude line art of a stack of books"}/>
            </div>
            <div className="border-scribble"></div>
            <table>
                <thead>
                    <tr>
                        <th scope='col'>Title</th>
                        <th scope='col'>Authors</th>
                        <th scope='col'>Venue</th>
                        <th scope='col'>Links</th>
                    </tr>
                </thead>
                <tbody>
                    {publications}
                </tbody>
            </table>
        </animated.section>
    );
}

export default memo(PublicationsSection)