export function loadBackground(backgrounds) {
    let random_bg = backgrounds[Math.floor(Math.random()*backgrounds.length)]
    /*for (let sec of document.querySelectorAll("section")) {
        sec.style.backgroundImage=`url(${random_bg})`;
    }*/
    return random_bg;
}

function shuffle(array) {
    var m = array.length, t, i;
    while (m) {
        i = Math.floor(Math.random() * m--);
        t = array[m];
        array[m] = array[i];
        array[i] = t;
    }
}

export function loadScribbles(scribbles) {
    shuffle(scribbles);
    let scribbleDivs = document.querySelectorAll(".border-scribble");
    console.log(scribbleDivs)
    for (let i = 0; i < scribbleDivs.length; i++) {
        console.log(scribbleDivs[i])
        scribbleDivs[i].style.backgroundImage=`url(${scribbles[i]})`;
    }
}